@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@200;300;400;600;700;900&display=swap);
:root {
   --btn-color: #f4f8ff;
   --btn-hover-color: #0ad4fa;
   --text-color: #a3a3a3;
   --text-color-dark: #616161;
   --text-color-btn: #fff;
   --font-size: 24px;
   --letter-spacing: -0.15px;
   --line-height: 48px;
}

* {
   box-sizing: border-box;
   margin: 0;
   padding: 0;
   scroll-behavior: smooth;
}

/* Responsive version */
.App {
   font-family: "Source Sans Pro", sans-serif;
   background-color: #ffffff;
   height: 100%;
   transition: background-color 0.5s ease;
   min-width: 320px;
}

.general-container {
   min-width: 320px;
   margin: 0 auto;
   border-right: 1px solid #d9d9d9;
   border-left: 1px solid #d9d9d9;
   background-color: #f4f8ff;
   transition: background-color 0.5s ease;
}

/* Desktop Version */
@media only screen and (min-width: 800px) {
   .App {
      text-align: center;
   }

   .general-container {
      max-width: 1440px;
   }
}

.App.darkMode {
   background-color: rgb(44, 44, 44);
   transition: background-color 0.5s ease;
}

.darkMode .general-container {
   background-color: rgb(44, 44, 44);
   transition: background-color 0.5s ease;
}

.darkMode .username {
   color: #0ad4fa;
   transition: color 0.5s ease;
}

.darkMode .total-points-btn {
   background-color: rgb(44, 44, 44);
   color: #0ad4fa;
   border: 1px solid #d9d9d9;
   transition: all 0.5s ease;
}

.darkMode .filter {
   background-color: rgb(44, 44, 44);
   color: var(--text-color);
   border: 1px solid #d9d9d9;
   transition: all 0.5s ease;
}

.darkMode .amount-products {
   color: var(--text-color);
   transition: all 0.5s ease;
}

.darkMode .products-container {
   background-color: #616161;
   transition: background-color 0.5s ease;
}

.darkMode .redeem-btn {
   color: #ffffff;
   background-color: #616161;
   opacity: 0.8;
   transition: all 0.5s ease;
}

.darkMode .redeem-message-container {
   color: #ffffff;
   background-color: #616161;
   opacity: 0.8;
   transition: all 0.5s ease;
}

.darkMode .product-category {
   color: #ffffff;
   transition: color 0.5s ease;
}

.darkMode .product-name {
   color: #ffffff;
   transition: color 0.5s ease;
}

.darkMode .add-points-container {
   background-color: rgb(217, 217, 217, 0.8);
   transition: opacity 1s, visibility 1s;
   transition: all 0.75s ease;
}

.darkMode .hide-container {
   background-color: rgb(217, 217, 217, 0.8);
   transition: opacity 1s, visibility 1s;
   transition: all 0.5s ease;
}

.darkMode .points-btn {
   background-color: #616161;
   opacity: 0.8;
   color: #ffffff;
   transition: all 0.5s ease;
}

.darkMode .points-message-container {
   background-color: #616161;
   opacity: 0.8;
   transition: all 0.5s ease;
}

.darkMode .points-message {
   color: #ffffff;
   transition: color 0.5s ease;
}

/* Responsive version */
header {
   min-width: 320px;
}

.logo-username-container {
   height: 60px;
   display: flex;
   align-items: center;
   justify-content: space-between;
}

.logo-darkmode-container {
   display: flex;
   justify-content: center;
   align-items: center;
}

.logo {
   height: 25px;
   padding-left: 10px;
}

.darkmode-container {
   border: 1px solid #d9d9d9;
   cursor: pointer;
   margin-left: 10px;
   border-radius: 8px;
   display: flex;
   justify-content: center;
   align-items: center;
   width: 30px;
   height: 30px;
   background-color: unset;
   box-shadow: 2px 2px 4px 0 rgba(0, 0, 0, 0.1);
}

.darkmode-btn {
   height: 25px;
}

.username-points-container {
   display: flex;
   width: 215px;
   margin-left: 15px;
   justify-content: center;
   align-items: center;
}

.username {
   color: var(--text-color-dark);
   transition: color 0.5s ease;
   width: 65px;
}

.total-points-btn {
   display: flex;
   margin-left: 10px;
   justify-content: center;
   align-items: center;
   background-color: var(--btn-color);
   width: 80px;
   height: 30px;
   border-radius: 100px;
   cursor: pointer;
   color: var(--text-color-dark);
   border: 1px solid #d9d9d9;
   transition: all 0.5s ease;
   box-shadow: 2px 2px 4px 0 rgba(0, 0, 0, 0.1);
}

.total-points-btn:hover {
   background-color: var(--btn-hover-color);
   cursor: pointer;
   transition: all 0.5s ease 0s;
   color: #ffffff;
}

.coin-icon {
   height: 24px;
   margin-left: 6px;
}

.add-points-header-container {
   position: relative;
}

.add-points-container {
   position: absolute;
   z-index: 1;
   left: 32%;
   top: 10px;
   display: flex;
   flex-direction: column;
   justify-content: center;
   align-items: center;
   background-color: rgb(255, 255, 255, 0.8);
   width: 200px;
   height: 290px;
   border-radius: 8px;
   visibility: visible;
   opacity: 1;
   transition: all 0.75s ease;
}

.hide-container {
   position: absolute;
   z-index: 1;
   left: 32%;
   top: 10px;
   display: flex;
   flex-direction: column;
   justify-content: center;
   align-items: center;
   background-color: rgb(255, 255, 255, 0.8);
   width: 200px;
   height: 290px;
   border-radius: 8px;
   visibility: hidden;
   opacity: 0;
   transition: all 0.75s ease;
}

.points-btn {
   display: flex;
   justify-content: center;
   align-items: center;
   background-color: var(--btn-color);
   width: 150px;
   height: 48px;
   border-radius: 100px;
   border: 1px solid #d9d9d9;
   color: var(--text-color-dark);
   margin: 5px 0 5px 0;
   transition: all 0.5s ease;
}

.points-btn:hover {
   background-color: var(--btn-hover-color);
   cursor: pointer;
   transition: all 0.5s ease 0s;
   color: #fff;
}

.h1-pic-container {
   overflow: hidden;
   position: relative;
   height: 110px;
   width: 100%;
}

.title {
   position: absolute;
   color: #fff;
   top: 50px;
   font-size: 35px;
   margin-left: 10px;
}

.header-pic {
   width: 100%;
   object-fit: cover;
}

.points-message-container {
   display: flex;
   justify-content: center;
   align-items: center;
   background-color: #f4f8ff;
   width: 165px;
   height: 60px;
   border-radius: 8px;
   border: 1px solid #d9d9d9;
   color: var(--text-color-dark);
   margin-top: 10px;
   transition: all 0.5s ease;
}

.points-message {
   display: flex;
   justify-content: center;
   align-items: center;
   border-radius: 8px;
   height: 60px;
   border: none;
   color: var(--text-color-dark);
   transition: color 0.5s ease;
}

.points-message-error {
   display: flex;
   justify-content: center;
   align-items: center;
   background-color: #fff;
   width: 100px;
   border-radius: 8px;
   border: none;
   color: var(--text-color-dark);
}

.success-icon {
   height: 24px;
   margin-left: 6px;
}

.error-icon {
   height: 24px;
   margin-left: 6px;
}

