      font-size: 64px;
   }

   .header-pic {
      height: 115%;
   }
}

/* Responsive version  */
.filters-container {
   display: flex;
   flex-wrap: wrap;
   min-width: 320px;
   height: 137px;
   margin: auto;
   align-items: center;
   justify-content: center;
   margin-bottom: 20px;
}

.amount-products {
   height: 48px;
   color: var(--text-color-dark);
   font-size: 16px;
   letter-spacing: var(--letter-spacing);
   line-height: var(--line-height);
   transition: all 0.5s ease 0s;
   margin-left: 10px;
}

.sort-by {
   font-size: 16px;
   color: var(--text-color);
   letter-spacing: var(--letter-spacing);
   line-height: var(--line-height);
   margin: 0 25px 0 15px;
}

.filter {
   background-color: var(--btn-color);
   border-radius: 100px;
   width: 100px;
   height: 30px;
   color: var(--text-color);
   font-size: 16px;
   letter-spacing: var(--letter-spacing);
   line-height: var(--line-height);
   text-align: center;
   border: 1px solid #d9d9d9;
   text-align: center;
   text-align-last: center;
   transition: all 0.5s ease;
   outline: none;
   margin-right: 15px;
   box-shadow: 2px 2px 4px 0 rgba(0, 0, 0, 0.1);
}

.filter:hover {
   background-color: var(--btn-hover-color);
   color: #fff;
   cursor: pointer;
   transition: all 0.5s ease 0s;
}

.purchase-history-btn {
   width: 150px;
   display: flex;
   justify-content: center;
   align-items: center;
}

.pagination-component {
   display: flex;
}

.hide-component {
   display: none;
}

/* Desktop Version */
@media only screen and (min-width: 800px) {
   .filters-container {
      height: 120px;
      align-items: flex-end;
   }

   .amount-products {
      width: 174px;
      margin: 0 24px 0 0;
      font-size: var(--font-size);
   }

   .vertical-line {
      border-left: 1px solid #d9d9d9;
      height: 49px;
      width: 1px;
      margin-right: 24px;
   }

   .sort-by {
      font-size: var(--font-size);
      margin: 0 24px 0 0;
   }

   .filter {
      width: 172px;
      height: 48px;
      margin-right: 24px;
      font-size: var(--font-size);
   }

   .purchase-history-btn {
      width: 220px;
      margin-right: 105px;
   }
}

/* Responsive version */
.arrow-left {
   height: 35px;
   margin-right: 5px;
   transition: background-color 0.5s ease 0s;
   border-radius: 100%;
   box-shadow: 2px 2px 4px 0 rgba(0, 0, 0, 0.1);
}

.arrow-right {
   height: 35px;
   transition: background-color 0.5s ease 0s;
   border-radius: 100%;
   box-shadow: 2px 2px 4px 0 rgba(0, 0, 0, 0.1);
}

.hide {
   display: none;
}

.arrow:hover {
   background-color: var(--btn-hover-color);
   cursor: pointer;
   transition: all 0.5s ease 0s;
   color: #fff;
}

/* Desktop Version */
@media only screen and (min-width: 800px) {
   .arrow-left {
      height: 48px;
      margin-right: 12px;
   }

   .arrow-right {
      height: 48px;
      border-radius: 100%;
   }
}

/* Responsive version */
.products-main-container {
   min-width: 320px;
   margin: auto;
   border-top: 1px solid #d9d9d9;
   display: flex;
   flex-wrap: wrap;
   padding-top: 42px;
}

.products-container {
   height: 276px;
   width: 276px;
   background-color: #ffffff;
   box-shadow: 2px 2px 4px 0 rgba(0, 0, 0, 0.1);
   overflow: hidden;
   margin-top: 42px;
   position: relative;
   margin: auto;
   transition: all 0.5s ease;
   margin-bottom: 20px;
}

.products-container-blue {
   height: 276px;
   width: 276px;
   background-color: #ffffff;
   box-shadow: 2px 2px 4px 0 rgba(0, 0, 0, 0.1);
   overflow: hidden;
   position: relative;
   margin: auto;
   margin-bottom: 20px;
}

.products-container-blue::after {
   content: "";
   width: 100%;
   height: 100%;
   background: linear-gradient(-180deg, #0ad4fa 0%, #25bbf1 100%);
   position: absolute;
   top: 0;
   left: 0;
   opacity: 0.8;
   z-index: 0;
   margin-bottom: 20px;
}

.pic-icon-container {
   height: 182px;
   width: 252px;
   display: flex;
   align-items: center;
   justify-content: center;
   margin: 0 auto;
}

.product-pic {
   max-width: 100%;
   max-height: 100%;
   margin: auto;
   margin-top: 10px;
}

.buy-icon {
   height: 30px;
   position: absolute;
   bottom: 220px;
   right: 15px;
   cursor: pointer;
   z-index: 11;
}

.missing-coins-container {
   opacity: 0.8;
   background-color: #616161;
   border-radius: 100px;
   width: 142px;
   height: 42px;
   display: flex;
   justify-content: center;
   align-items: center;
   position: absolute;
   bottom: 220px;
   right: 15px;
}

.missing-coins-p {
   font-size: 14px;
   color: #ffffff;
   letter-spacing: -0.03px;
   margin-right: 6px;
}

.missing-coins-icon {
   height: 20px;
}

.redeem-main-container {
   height: 182px;
   width: 252px;
   display: flex;
   align-items: center;
   justify-content: center;
   margin: 0 auto;
}

.reedem-container {
   position: absolute;
   z-index: 10;
}

.coins-amount-container {
   display: flex;
   justify-content: center;
   align-items: center;
   margin-top: 125px;
}

.coins-p {
   font-size: 36px;
   color: #ffffff;
   letter-spacing: -0.08px;
   margin-right: 10px;
}

.coins-p-history {
   font-size: 24px;
   color: #ffffff;
   letter-spacing: -0.08px;
   margin-right: 10px;
}

.coins-icon {
   height: 26px;
}

.redeem-btn {
   font-size: 18px;
   color: var(--text-color-dark);
   letter-spacing: -0.04px;
   width: 228px;
   height: 42px;
   border-radius: 21px;
   background-color: #fff;
   display: flex;
   align-items: center;
   justify-content: center;
   margin-top: 12px;
   border: none;
   cursor: pointer;
   transition: all 0.5s ease;
}

.p-container {
   height: 81px;
   text-align: left;
   width: 228px;
   margin: 0 auto;
   margin-top: 20px;
   border-top: 1px solid #d9d9d9;
}

.product-category {
   font-size: 16px;
   color: var(--text-color);
   letter-spacing: -0.04px;
   padding-top: 10px;
   transition: color 0.5s ease;
}

.product-name {
   font-size: 18px;
   color: var(--text-color);
   letter-spacing: -0.04px;
   transition: color 0.5s ease;
}

.message-icon {
   height: 40px;
   margin-left: 10px;
}

.redeem-message-container {
   display: flex;
   justify-content: center;
   align-items: center;
   height: 60px;
   font-size: 18px;
   color: var(--text-color);
   letter-spacing: -0.04px;
   margin-top: 10px;
   background-color: #fff;
   border-radius: 8px;
   transition: all 0.5s ease;
}

.redeem-error-container {
   display: flex;
   justify-content: center;
   align-items: center;
   height: 90px;
   font-size: 18px;
   color: var(--text-color);
   letter-spacing: -0.04px;
   margin-top: 10px;
   background-color: #fff;
   border-radius: 8px;
}

.error-p {
   width: 150px;
}

.loading {
   border: 16px solid #f3f3f3;
   border-top: 16px solid #0ad4fa;
   border-radius: 50%;
   width: 80px;
   height: 80px;
   -webkit-animation: spin 2s linear infinite;
           animation: spin 2s linear infinite;
   margin: 0 auto;
}

@-webkit-keyframes spin {
   0% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
   }
   100% {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
   }
}

@keyframes spin {
   0% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
   }
   100% {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
   }
}

/* Desktop Version */
@media only screen and (min-width: 800px) {
   .products-main-container {
      width: 1176px;
      margin-top: 25px;
      display: grid;
      grid-template-columns: 25% 25% 25% 25%;
      grid-row-gap: 24px;
      padding-top: 42px;
   }

   .buy-icon {
      height: 42px;
   }
}

